import React from 'react'
import { Navbar, Nav, NavItem } from 'react-bootstrap'
import { Link } from 'react-router'
import Logo from '../logo'

function Header ({channelTitle, logOut}) {
  return (
    <Navbar inverse>
      <Navbar.Header>
        <Navbar.Brand>
          <Link to={'/'}>
            <Logo />
          </Link>
        </Navbar.Brand>
        <Navbar.Toggle />
      </Navbar.Header>
      <Navbar.Collapse>
        <Nav pullRight>
          <NavItem eventKey={1}>Log In</NavItem>
        </Nav>
        { channelTitle && (
          <Nav pullRight>
            <NavItem eventKey={1}>{channelTitle}</NavItem>
          </Nav>
        )}
      </Navbar.Collapse>
    </Navbar>
  )
}

export default Header



